import React from "react"
import PropTypes from "prop-types"
import { Helmet } from "react-helmet"
import { Link, graphql } from "gatsby"

// Utilities
import kebabCase from "lodash/kebabCase"

// Components
import Layout from "../components/layout"
import Sidebar from "../components/sidebar"
import MailList from "../components/maillist"

const CategoryPage = ({
  data: {
    allMarkdownRemark: { group },
    site: {
      siteMetadata: { title },
    },
  },
}) => (
  <Layout>
    <Helmet title={title} />
    <div class="col-md-4" id="content-desktop">
      <Sidebar />
    </div>
    <div class="col-md-8">
      <br />
      <div class="alert alert-secondary" role="alert">
        <h4 class="my-1">
          جميع الأقسام <i class="fa fa-tags fa-sm"></i>
        </h4>
      </div>
      <div class="alert alert-info" role="alert">
        يمكنك تصفح الموضوعات عن طريق الأقسام
      </div>
      <div class="card-body">
        <ul dir="rtl">
          {group.map(category => (
            <li key={category.fieldValue} class="card mb-4">
              <Link
                to={`/category/${kebabCase(category.fieldValue)}/`}
                class="stretched-link text-secondary"
              >
                <b>
                  <i class="fa fa-tags fa-sm"></i> {category.fieldValue}
                  {"  "}
                  <span class="badge badge-danger">{category.totalCount}</span>
                </b>
              </Link>
            </li>
          ))}
        </ul>
      </div>
      <div id="content-mobile">
        <div class="p-3 mb-2 bg-success text-white">القائمة البريدية</div>
        <MailList />
      </div>
      <hr />
    </div>
  </Layout>
)

CategoryPage.propTypes = {
  data: PropTypes.shape({
    allMarkdownRemark: PropTypes.shape({
      group: PropTypes.arrayOf(
        PropTypes.shape({
          fieldValue: PropTypes.string.isRequired,
          totalCount: PropTypes.number.isRequired,
        }).isRequired
      ),
    }),
    site: PropTypes.shape({
      siteMetadata: PropTypes.shape({
        title: PropTypes.string.isRequired,
      }),
    }),
  }),
}

export default CategoryPage

export const pageQuery = graphql`
  query {
    site {
      siteMetadata {
        title
      }
    }
    allMarkdownRemark(limit: 2000) {
      group(field: frontmatter___category) {
        fieldValue
        totalCount
      }
    }
  }
`
